import React from 'react'
import { useSelector } from 'react-redux'
import PageLayout from '../../layouts/PageLayout/PageLayout'
import { ConstTranslate } from '../../constants/translate'
import { utilService } from '../../services/util.service'

const UserCreditsHistory = () => {
    const { currentUser } = useSelector(state => state.user)

    return (
        <PageLayout title={ConstTranslate.CREDITS_HISTORY}>
            <ul className="flex f-dir-col gap--10">
                {
                    currentUser.creditTransactions.map(trans => {
                        return (
                            <li key={trans._id} className="credit-history__item">
                                <div className="credit-history__item__details">
                                    <h6>{trans.creditName}</h6>
                                    <p>{utilService.formatTimestamp(new Date(trans.createdAt))}</p>
                                </div>

                                <div className="credit-history__item__info">
                                    <p className="credit-history__item__quantity">{trans.creditQuantity} {ConstTranslate.CREDITS}</p>
                                    <p className="credit-history__item__price">${trans.packagePrice}</p>
                                </div>
                            </li>
                        )
                    })
                }
            </ul>
        </PageLayout>
    )
}

export default UserCreditsHistory
